<template>
  <div class="nav_burger">
    <button @click="burgerActivator">
      <img src="../icons/hamburger_icon.svg" alt="" class="nav_berger__ico" />
    </button>
    <div class="modal_window" v-if="burgerPanelIsActive">
      <button @click="burgerActivator">
        <img src="../icons/hamburger_icon.svg" alt="" class="nav_berger__ico" />
      </button>
      <div class="nav_wrapper">
        <calendar-switch></calendar-switch>
        <theme-switch></theme-switch>
        <donat-link></donat-link>
        <disk-link></disk-link>
      </div>
    </div>
  </div>
</template>

<script>
import CalendarSwitch from "./calendarSwitch.vue";
import DiskLink from "./diskLink.vue";
import DonatLink from "./donatLink.vue";
import navPanel from "./navPanel.vue";
import ThemeSwitch from "./themeSwitch.vue";
export default {
  components: { navPanel, ThemeSwitch, CalendarSwitch, DonatLink, DiskLink },
  data() {
    return {
      burgerPanelIsActive: false,
    };
  },
  methods: {
    burgerActivator() {
      this.burgerPanelIsActive
        ? (this.burgerPanelIsActive = false)
        : (this.burgerPanelIsActive = true);
    },
  },
};
</script>

