<template>
  <header class="header">
    <main-logo></main-logo>
    <nav-panel v-if="$store.state.appWight >= 850"></nav-panel>
    <nav-burger v-if="$store.state.appWight <= 850"></nav-burger>
  </header>
</template>

<script>
import calendarSwitch from "@/components/calendarSwitch";
import ThemeSwitch from "./themeSwitch.vue";
import NavPanel from "./navPanel.vue";
import MainLogo from "./mainLogo.vue";
import NavBurger from "./navBurger.vue";

export default {
  components: { calendarSwitch, ThemeSwitch, NavPanel, MainLogo, NavBurger },
  methods: {
    checkStorageDarkMode() {
      let dark = localStorage.darkMode;
      if (dark === undefined) return 0;
      if (dark == "true") {
        this.$store.commit("switchDarkMode");
      }
    },
    darkMode() {
      this.$store.commit("switchDarkMode");
      if (this.$store.state.darkMode) {
        let bodyElement = document.body;
        bodyElement.classList.add("darkMode");
        localStorage.setItem("darkMode", true);
      } else {
        let bodyElement = document.body;
        bodyElement.classList.remove("darkMode");
        localStorage.setItem("darkMode", false);
      }
    },
    chackCalendarMode() {
      let calendar = localStorage.calendarMode;
      if (calendar === undefined) return 0;
      if (calendar == "true") {
        this.$store.commit("switchCalendarMode");
      }
    },
    calendarMode() {
      this.$store.commit("switchCalendarMode");

      if (this.$store.state.calendarMode) {
        localStorage.setItem("calendarMode", true);
      } else {
        localStorage.setItem("calendarMode", false);
      }
    },
  },
  mounted() {
    this.checkStorageDarkMode();

    this.chackCalendarMode();
  },
};
</script>


<style lang="scss">
.header {
  display: flex;
  width: 100%;
  transition-property: all;
  transition-duration: 0.3s;
  padding: 0 10%;
  font-family: "Jura", sans-serif;
  font-weight: 400;
  font-size: 26px;
  color: #000000;

  &__logo {
    margin-top: 8px;
  }
  &__nav {
    margin-left: auto;
    width: 750px;
    min-height: 70px;

    display: flex;
    justify-content: space-between;
    &_donat {
      display: flex;
      align-items: center;
      justify-content: center;
      width: px;
      height: 70px;

      &::before {
        content: "";
        background-image: url("../icons/donat.svg") 0 0 / cover no-repeat;
        background: url("../icons/donat.svg") 0 0 / cover no-repeat;
        width: 36px;
        height: 36px;
        margin-right: 10px;
        background-size: cover;
        fill: #a48686;
      }
    }
    &_files {
      display: flex;
      align-items: center;
      justify-content: center;

      height: 70px;

      &::before {
        content: "";
        background: url("../icons/files.svg") 0 0 / cover no-repeat;
        width: 34px;
        height: 34px;
        margin-right: 10px;
        background-size: cover;
      }
    }

    &_calendar-switch {
      width: 120px;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;

      &_button {
        position: relative;
        &::after {
          right: -55px;
          width: 36px;
          height: 36px;
          position: absolute;
          content: "";
          background-size: cover;
          background: url("../icons/calendar.svg") 0 0 / cover no-repeat;
        }
        &::before {
          left: -65px;
          width: 48px;
          height: 44px;
          position: absolute;
          content: "";
          background-size: cover;
          background: url("../icons/spis.svg") 0 0 / cover no-repeat;
        }
        display: flex;
        align-items: center;
        height: 21px;
        width: 43px;
        background-color: rgb(0, 0, 0);
        border-radius: 90px;
        padding: 0 5px;
        &_selector {
          background-color: #fff;
          width: 11px;
          height: 11px;
          border-radius: 90px;
        }
      }
    }

    &_theme-switch {
      width: 120px;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      &_button {
        position: relative;
        &::after {
          right: -32px;
          width: 20px;
          height: 20px;
          position: absolute;
          content: "";
          background-size: cover;
          background: url("../icons/theme_switch_off.svg") 0 0 / cover no-repeat;
        }
        &::before {
          left: -35px;
          width: 25px;
          height: 25px;
          position: absolute;
          content: "";
          background-size: cover;
          background: url("../icons/theme_switch_ON.svg") 0 0 / cover no-repeat;
        }
        display: flex;
        align-items: center;
        height: 21px;
        width: 43px;
        background-color: rgb(0, 0, 0);
        border-radius: 90px;
        padding: 0 5px;
        &_selector {
          transition: all 2s ease 0s;
          background-color: #fff;
          width: 11px;
          height: 11px;
          border-radius: 90px;
        }
      }
    }
  }
}
</style>