<template>
  <div class="wrapper">
    <timetable-date></timetable-date>
  </div>
  <div class="add_lessonse_wrapper">
    <div class="add_text">Изменение занятий</div>
    <div class="table_upper_tegs">
      <div class="time">Время</div>
      <div class="classroom">Аудитория</div>
      <div class="lesson_name">Предмет</div>
      <div class="teacher">Преподаватель</div>
      <div class="lesson_type">Тип</div>
    </div>
    <div class="add_lessense_table">
      <div class="list" v-for="lesson in newLessonseArr" :key="lesson">
        <add-lesson
          ref="less"
          @push="lessonsePush"
          :newLesson="lesson"
          @deleteLesson="deleteLesson"
        >
        </add-lesson>
      </div>

      <button class="add_lisson_btn" @click="addNewLesson()">
        Добавить
        <img src="../icons/add_btn.svg" alt="" class="add_icon" />
      </button>
    </div>

    <div class="personal_key">
      <div class="personal_kay__text">Персональный ключ</div>
      <input type="password" class="personal_kay__input" />
      <button class="save_btn" @click="save()">Сохранить</button>
    </div>
  </div>
</template>

<script>
import timetableDate from "@/components/timetableDate.vue";
import AddLesson from "@/components/addLesson.vue";
export default {
  components: { timetableDate, AddLesson },
  data() {
    return {
      newLessonseArr: [
        {
          id: Date.now(),
        },
      ],
      newLessonseArrData: [],
    };
  },
  methods: {
    lessonsePush(lesson) {
      console.log("добавляю урок");
      this.newLessonseArrData.push(lesson);
    },

    deleteLesson(newLesson) {
      this.newLessonseArr = this.newLessonseArr.filter(
        (l) => l.id !== newLesson.id
      );
    },
    addNewLesson() {
      this.newLessonseArr.push({
        id: Date.now(),
      });
    },
    async save() {
      for (let i = 0; i < this.newLessonseArr.length; i++) {
        this.$refs.less[i].pushLesson();
      }

      let res = await fetch("http://rospis-kgeu.ru/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8",
        },
        body: JSON.stringify(this.newLessonseArrData),
      });
      let result = await res.text();
      console.log(result);
      await this.$store.dispatch("getlessonseFromMongo");
      this.$store.commit("getLessonsToday");
      this.$router.push({ path: "/" });
      this.newLessonseArrData.length = 0;
    },
  },
};
</script>
 

