import { createStore } from "vuex";

export default createStore({
   state: {
      lessons: [],
      lessonsToday: [],
      darkMode: false,
      calendarMode: false,
      dateToday: "",
      dayOfWeek: "",
      toDay: new Date(),
      todayWatch: 0,
      calendar: [],
      toDayStatic: new Date(),
      appWight: document.documentElement.scrollWidth
   },
   getters: {
      getlessonsTodayStatus(state) {
         return state.lessonsToday.length == 0 ? true : false
      },
      getCalendarModeStatus(state) {
         return state.calendarMode
      }

   },
   mutations: {
      getdayOfWeek(state) {
         switch (state.toDay.getDay()) {
            case 0:
               state.dayOfWeek = "Воскресенье";
               break;
            case 1:
               state.dayOfWeek = "Понедельник";
               break;
            case 2:
               state.dayOfWeek = "Вторник";
               break;
            case 3:
               state.dayOfWeek = "Среда";
               break;
            case 4:
               state.dayOfWeek = "Четверг";
               break;
            case 5:
               state.dayOfWeek = "Пятница";
               break;
            case 6:
               state.dayOfWeek = "Суббота";
               break;
         }
         let DD = state.toDay.getDate(),
            MM = state.toDay.getMonth(),
            YYYY = state.toDay.getFullYear();
         state.dateToday = `${DD}.${MM + 1}.${YYYY}`;
      },
      increaseDate(state) {
         state.toDay.setTime(state.toDay.getTime() + 86400000);
         state.todayWatch += 1
      },
      reduceDate(state) {
         state.toDay.setTime(state.toDay.getTime() - 86400000);
         state.todayWatch += 1
      },
      calendarInit(state) {
         state.calendar = [];
         // По новой

         // Получаем количество дней в текущем месяце
         state.toDay.getDaysInMounth = function () {
            let date1 = new Date(this.getFullYear(), this.getMonth(), 1);
            let date2 = new Date(this.getFullYear(), this.getMonth() + 1, 1);
            let date3 = Math.round((date2 - date1) / 1000 / 3600 / 24);
            return date3;
         };
         console.log(state.toDay.getDaysInMounth());

         state.toDay.getDayWorld = function () {
            let todayClone = new Date(this.getFullYear(), this.getMonth());
            switch (todayClone.getDay()) {
               case 0:
                  return 6;
                  break;
               case 1:
                  return 0;
                  break;
               case 2:
                  return 1;
                  break;
               case 3:
                  return 2;
                  break;
               case 4:
                  return 3;
                  break;
               case 5:
                  return 4;
                  break;
               case 6:
                  return 5;
                  break;
            }
         };

         console.log(state.toDay.getDayWorld());

         // добавляем нули в перед
         for (let i = 1; i < state.toDay.getDayWorld() + 1; i++) {
            state.calendar.push(0);
         }

         // добовляем числа месяца в массив
         for (let i = 1; i <= state.toDay.getDaysInMounth(); i++) {
            state.calendar.push(i);
         }
      },
      switchDateFromCalendar(state, day) {
         state.toDay.setDate(day)
      },
      switchDarkMode(state) {

         if (state.darkMode) {
            document.body.classList.remove('darkMode')
            state.darkMode = false;
            localStorage.setItem('darkMode', false);
         } else {
            document.body.classList.add('darkMode')
            state.darkMode = true;
            localStorage.setItem('darkMode', true);

         }
      },
      getLessonsToday(state) {
         state.lessonsToday.length = 0
         state.lessons.forEach(function (item) {
            if (item.lessonStart.getMonth() === state.toDay.getMonth()
               &&
               item.lessonStart.getDate() === state.toDay.getDate())


               state.lessonsToday.push(item)

         })
      },
      switchCalendarMode(state) {
         console.log(111)
         if (state.calendarMode) {

            state.calendarMode = false;

            localStorage.setItem('calendarMode', false);

         } else {
            state.calendarMode = true;

            localStorage.setItem('calendarMode', true);
         }
      },
      updateLessonse(state, lessonse) {
         state.lessons = lessonse
      }
   },
   actions: {
      async getlessonseFromMongo(ctx) {
         try {
            let response = await fetch("http://rospis-kgeu.ru/api/lessonse");
            let les = await response.json();
            les.forEach((item) => {
               item.lessonStart = new Date(item.lessonStart)
            })
            ctx.commit("updateLessonse", les)
         } catch (e) {
            console.error("Не получилось подключиться к БД " + e)
         }



      },
      async addNewLessonse(ctx) {

      }
   }

})