<template >
  <header-panel></header-panel>
  <router-view></router-view>
  <main></main>
</template>
<script>
import headerPanel from "./components/headerPanel.vue";
import Main from "./pages/Main.vue";
export default {
  components: { headerPanel, Main },
  data() {
    return {
      darkModeState: this.$store.state.darkMode,
    };
  },
  methods: {
    initDarkMode() {
      if (this.$store.state.darkMode != localStorage.getItem(darkMode)) {
        this.$store.commit("switchDarkMode");
      }
    },
    init() {
      this.$store.commit("getdayOfWeek");
      this.$store.commit("calendarInit");
      this.$store.commit("getLessonsToday");
    },
  },
  async mounted() {
    this.init();
    await this.$store.dispatch("getlessonseFromMongo");
    this.init();
    this.initDarkMode;
  },
  watch: {
    darkModeState() {
      let htmlElement = document.documentElement;
      let theme = localStorage.getItem("theme");

      if (theme === "dark") {
        htmlElement.setAttribute("theme", "dark");
        this.darkMode = true;
      } else {
        htmlElement.setAttribute("theme", "light");
        this.darkMode = false;
      }
    },
  },
  computed: {},
};
</script>

