<template  >
  <div class="wrapper_add-lesson">
    <form>
      <input class="hours" type="text" v-model="hours" />
      <input class="minutes" type="text" v-model="minutes" />
      <input class="classrom" type="text" v-model="classrom" />
      <input class="lessonse-name" type="text" v-model="lessonseName" />
      <input class="name" type="text" v-model="teacher" />
      <div class="type_select">
        <div class="form_radio_btn">
          <input
            type="radio"
            :id="idFormFirst"
            value="Лекция"
            v-model="lessonType"
          />
          <label :for="idFormFirst">Л</label>
        </div>
        <div class="form_radio_btn">
          <input
            type="radio"
            :id="idFormFirst + 1"
            value="Экзамен"
            v-model="lessonType"
          />
          <label :for="idFormFirst + 1">Э</label>
        </div>
      </div>
      <button class="delete" @click="deleteLesson">X</button>
    </form>
  </div>
</template>

<script>
export default {
  props: {
    newLesson: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      idFormFirst: this.newLesson.id,

      minutes: undefined,
      hours: undefined,
      classrom: undefined,
      lessonseName: undefined,
      teacher: undefined,
      lessonType: undefined,

      lesson: {
        lessonStart: new Date(2022, 3, 21, 11, 20),
        lessonName: undefined,
        classroom: undefined,
        teacher: undefined,
        lessonType: undefined,
      },
    };
  },
  methods: {
    pushLesson() {
      const newLesson = {
        id: this.newLesson.id,
        lessonStart: new Date(
          this.$store.state.toDay.getFullYear(),
          this.$store.state.toDay.getMonth(),
          this.$store.state.toDay.getDate(),
          this.hours,
          this.minutes
        ),
        lessonName: this.lessonseName,
        classroom: this.classrom,
        teacher: this.teacher,
        lessonType: this.lessonType,
      };

      this.$emit("push", newLesson);
    },

    deleteLesson() {
      this.$emit("deleteLesson", this.newLesson);
    },
  },
  computed: {},
  watch: {
    minutes(n, o) {
      if (n > 60) {
        this.minutes = o;
      } else if (n.length > 2) {
        this.minutes = o.replace(/\D/g, "");
      } else this.minutes = n.replace(/\D/g, "");
    },
    hours(n, o) {
      console.log("Фиксирую изменение");
      if (n >= 24) {
        this.hours = o;
      } else if (n.length > 2) {
        this.hours = o.replace(/\D/g, "");
      } else this.hours = n.replace(/\D/g, "");
    },
    classrom(n, o) {
      this.classrom = n.toUpperCase();
    },
  },
};
</script>

<style lang="scss" scoped>
.type_select {
  overflow: hidden;
  height: 63px;
  display: inline-block;
  border-radius: 5px;
  background-color: #fff;
}
.form_radio_btn {
  width: 36px;
  display: inline-block;
  height: 63px;
  vertical-align: top;
}
.form_radio_btn input[type="radio"] {
  display: none;
}
.form_radio_btn label {
  height: 63px;
  display: inline-block;
  cursor: pointer;
  padding: 0px 15px;
  line-height: 62px;
  overflow: hidden;
  user-select: none;
}
/* Checked */
.form_radio_btn input[type="radio"]:checked + label {
  background: #92d89d;
  overflow: hidden;
  height: 100%;
}

/* Hover */
.form_radio_btn label:hover {
  color: rgb(118, 38, 38);
}

.wrapper_add-lesson {
  font-family: "Lexend Deca", sans-serif;
  width: 100%;
  border-radius: 5px;
  display: flex;
  margin-top: 21px;
}
.hours {
  text-align: center;
  vertical-align: top;
  border-radius: 5px;
  width: 55px;
  height: 62px;

  font-size: 25px;
}
.minutes {
  font-size: 25px;
  text-align: center;
  vertical-align: top;
  margin-left: 15px;
  border-radius: 5px;
  width: 55px;
  height: 62px;
}
.classrom {
  font-size: 25px;
  text-align: center;
  vertical-align: top;
  margin-left: 31px;
  border-radius: 5px;
  width: 85px;
  height: 62px;
}
.lessonse-name {
  font-size: 20px;
  text-align: center;
  vertical-align: top;
  margin-left: 27px;
  border-radius: 5px;
  width: 432px;
  height: 62px;
}
.name {
  font-size: 17px;
  text-align: center;
  vertical-align: top;
  margin-left: 22px;
  border-radius: 5px;
  width: 174px;
  height: 62px;
  margin-right: 24px;
}

.type-select {
  vertical-align: top;
  margin-left: 24px;
  border-radius: 5px;
  width: 72px;
  height: 62px;
}

.delete {
  font-size: 20px;
  vertical-align: top;
  margin-left: 17px;
  background: #ee8989;
  border-radius: 5px;
  width: 40px;
  height: 62px;
  font-family: "Lexend Mega", sans-serif;
}
</style>