<template>
  <div class="calendar">
    <div class="calendar__date">
      <div class="date-day">ПН</div>
      <div class="date-day">ВТ</div>
      <div class="date-day">СР</div>
      <div class="date-day">ЧТ</div>
      <div class="date-day">ПТ</div>
      <div class="date-day red">СБ</div>
      <div class="date-day red">ВС</div>
    </div>
    <div class="calendar__date_namber-box">
      <div class="calendar__date_namber-box_wrapper">
        <button
          class="date-namber"
          v-for="elem in $store.state.calendar"
          :style="{
            visibility: elem == 0 ? 'hidden' : 'visible',
            color:
              elem == $store.state.toDayStatic.getDate() &&
              $store.state.toDay.getMonth() ===
                $store.state.toDayStatic.getMonth()
                ? 'red'
                : 'black',
          }"
          @click="switchDateFromCalendar(elem)"
          :class="buttonClassObject(elem)"
        >
          {{ elem }}
        </button>
      </div>
      <div class="lesson-type">
        <div class="lesson-type__lecture">
          <div class="lesson-type__lecture_color"></div>
          <div class="lesson-type__lecture_name">- ЛЕКЦИЯ</div>
        </div>
        <div class="lesson-type__exam">
          <div class="lesson-type__exam_color"></div>
          <div class="lesson-type__exam_name">- ЭКЗАМЕН/ЗАЧЕТ</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store";
export default {
  data() {
    return {
      calendar: [],
      today: this.$store.state.toDay,
      dateTodayy: this.$store.state.dayOfWeek,
    };
  },
  methods: {
    switchDateFromCalendar(day) {
      this.$store.commit("switchDateFromCalendar", day);
      this.$store.commit("getdayOfWeek");
      this.$store.commit("calendarInit");
      this.$store.commit("getLessonsToday");
    },
    buttonClassObject(elem) {
      return {
        animation: elem == this.$store.state.toDay.getDate() ? true : false,
        lecture: this.$store.state.lessons.find(
          (item) =>
            elem == item.lessonStart.getDate() &&
            item.lessonType == "Лекция" &&
            this.$store.state.toDay.getMonth() == item.lessonStart.getMonth()
        ),
        exam: this.$store.state.lessons.find(
          (item) =>
            elem == item.lessonStart.getDate() &&
            item.lessonType == "Экзамен" &&
            this.$store.state.toDay.getMonth() == item.lessonStart.getMonth()
        ),
      };
    },
  },
  mounted() {},
  watch: {},
  computed: {},
};
</script>


<style lang="scss">
.calendar {
  animation: ani 2s forwards;
  width: 710px;
  min-height: 485px;

  margin: 0 auto;
  margin-top: 30px;
  &__date {
    min-height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    &_namber-box {
      display: block;
      min-height: 440px;
      width: 100%;
      border-top: 4px solid #000000;
      border-bottom: 4px solid #000000;
      &_wrapper {
        width: 100%;

        display: flex;
        flex-wrap: wrap;
        justify-content: start;
      }
    }
  }
}
.date-day {
  width: 60px;
  height: 34px;
  font-family: "Lexend Deca", sans-serif;
  margin: 14px 21px 0 20px;
  font-size: 24px;
  line-height: 30px;
}

.date-namber {
  display: flex;
  margin: 14px 21px 0 20px;
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 12px;
  font-family: Lexend Deca;
  font-style: normal;
  font-size: 36px;
  line-height: 45px;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  &:hover {
    box-shadow: inset 0 0 0 5px #53ea93;
  }
}

.lesson-type {
  display: flex;
  margin-top: 20px;
  margin-bottom: 13px;
  padding-left: 23px;
  height: 20px;
  width: 100%;
  &__lecture {
    display: flex;

    height: 30px;
    &_color {
      width: 15px;
      height: 15px;
      background: #66ff58;
    }
    &_name {
      margin-top: 1px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      margin-left: 10px;
    }
  }
  &__exam {
    margin-left: 40px;
    display: flex;
    &_color {
      width: 15px;
      height: 15px;
      background: #fb0505;
    }
    &_name {
      margin-top: 1px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      margin-left: 10px;
    }
  }
}

@keyframes lesson {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
    box-shadow: 0 0 0 0 rgba(4, 255, 16, 0.7);
  }
  70% {
    -moz-box-shadow: 0 0 0 10px rgba(204, 169, 44, 0);
    box-shadow: 0 0 0 15px rgba(11, 231, 59, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
    box-shadow: 0 0 0 0 rgba(7, 243, 19, 0);
  }
}
.animation {
  animation: lesson 1.8s infinite;
}

.lecture {
  background-color: #deffdb;
}
.exam {
  background-color: #febbbb;
}
</style>