<template>
  <div class="timetable">
    <div class="timetable__date">
      <button class="timetable__date_leftBTN" @click="reduceDate">
        <img src="@/icons/swich_btn.svg" alt="a" />
      </button>
      <div class="timetable__date_scoreboard">
        <div class="timetable__date_scoreboard_day-of-week">
          {{ $store.state.dayOfWeek }}
        </div>
        <div class="timetable__date_scoreboard_number">
          {{ $store.state.dateToday }}
        </div>
      </div>

      <button class="timetable__date_rightBTN" @click="increaseDate">
        <img src="@/icons/swich_btn.svg" alt="a" />
      </button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    increaseDate() {
      this.$store.commit("increaseDate");
      this.$store.commit("getdayOfWeek");
      this.$store.commit("calendarInit");
      this.$store.commit("getLessonsToday");
    },
    reduceDate() {
      this.$store.commit("reduceDate");
      this.$store.commit("getdayOfWeek");
      this.$store.commit("calendarInit");
      this.$store.commit("getLessonsToday");
    },
  },
};
</script>

