<template>
  <div class="wrapperLessinseList">
    <div
      class="timetable__lessons"
      v-for="(item, i) in $store.state.lessonsToday"
    >
      <div class="timetable__lessons_number">
        <div class="timetable__lessons_number_slot">{{ i + 1 }}</div>
      </div>
      <div class="timetable__lessons_time">
        <div class="timetable__lessons_time_slot">
          {{
            `${item.lessonStart.getHours()}:${
              item.lessonStart.getMinutes() == 0
                ? "00"
                : item.lessonStart.getMinutes()
            }`
          }}
        </div>
      </div>
      <div class="timetable__lessons_wrapper">
        <div class="lesson" id="qqq">
          <div
            class="lesson__cabinet"
            :style="{
              fontSize:
                item.classroom.length > 5 && this.width >= 500 ? '25px' : '',
            }"
          >
            {{ item.classroom }}
          </div>
          <div
            class="lesson__thing"
            :style="{
              fontSize:
                item.lessonName.length > 45 && this.width >= 500 ? '20px' : '',
            }"
          >
            {{ item.lessonName }}
          </div>
          <div class="lesson__add-information">
            <div class="lesson__add-information_teacher">
              {{ item.teacher }}
            </div>
            <div class="lesson__add-information_type">
              {{ item.lessonType }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      width: window.innerWidth,
    };
  },
};
</script>

<style lang="scss">
.wrapperLessinseList {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  animation: ani 1.5s forwards;
  margin-top: 50px;
}
</style>